export default{
  data() {
    return {
      title: '',
      token: '',
      showDel: false,
      code_word: '获取验证码',
      mobile: '',
      code: '',
      password: '',
      showInfo: false,             // 验证码发送状态
      ishide: false,              // 密码的显示或隐藏
      check_code: false,          // 验证码是否正确
    }
  },
  // onLoad(option) {
  //   console.log(option.token)
  //   this.token = option.token
  // },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    },
    // 验证码输入
    onInputCode(e) {
      let that = this
      let code = e.detail.value
      if(code.length === 4 && this.showInfo) {
        this.$http({
          url: this.$api.smscodeyz,
          method: 'POST',
          data: {
            mobile: this.mobile,
            code: this.code
          },
          cb: (err, res) => {
            if(!err && res.code === 1) {
              that.check_code = true
            }else if (res.code === 0 && res.msg){
              uni.showToast({
                title: res.msg,
                icon: 'none'
              })
            } if(err) {
              uni.showToast({
                title: '手机验证码校验失败',
                icon: 'none'
              })
            }
          }
        })
      }
    },
    // 手机号码输入
    onInput(e) {
      let value = e.detail.value
      if(value) {
        this.showDel = true
        return
      }
      this.showDel = false
    },
    sureAction() {
      let code_mobile = uni.getStorageSync('code_mobile');
      let that = this
      console.log(Number(code_mobile) !== that.mobile)
      if(!that.showInfo) {
        uni.showToast({
          title: '请先获取手机验证码',
          icon: 'none'
        })
        return
      }
      if(!that.code || that.code.length !== 4) {
        uni.showToast({
          title: '验证码格式错误',
          icon: 'none'
        })
        return
      }
      if(!that.password) {
        uni.showToast({
          title: '请输入密码',
          icon: 'none'
        })
        return
      }
      if(Number(code_mobile) !== that.mobile) {
        uni.showToast({
          title: '手机号不一致，请重新获取验证码',
          icon: 'none'
        })
        that.showInfo = false
        return
      }
      if(!that.check_code) {
        uni.showToast({
          title: '验证码错误',
          icon: 'none'
        })
        return
      }
      // 绑定手机
      let data = {
        mobile: that.mobile,
        password: that.password
      }
      console.log(JSON.stringify(data))
      that.$http({
        url: that.$api.otherregister,
        method: 'POST',
        data: data,
        cb: (err, res) => {
          if(!err && res.code === 1) {
            uni.showToast({
              title: '绑定手机号成功',
              icon: 'none'
            })
            console.log(JSON.stringify(res))
            let userinfo = {
              mobile: res.data.info.mobile,
              token: res.data.info.token
            }
            
            // 存储token信息
            that.$store.commit('login', userinfo)
            uni.setStorage({
              key: 'userinfo',
              data: userinfo,
              success: function () {
                uni.switchTab({
                  url: '../index/index'
                })
              }
            })
          } else if (res.code === 0 && res.msg){
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } if(err) {
            uni.showToast({
              title: '绑定手机号失败',
              icon: 'none'
            })
          }
        }
      })
    },
    
    // 获取验证码
    getCode() {
      uni.setStorageSync('code_mobile', this.mobile);
      let value = /^1[3456789]\d{9}$/.test(this.mobile)
      let that = this
      if (!value){
        uni.showToast({
          title: '请输入正确的手机号码',
          icon: 'none'
        })
        return
      }
      this.$http({
        url: this.$api.sendcode,
        method: 'POST',
        data: {
          mobile: this.mobile
        },
        cb: (err, res) => {
          if(!err && res.code === 1) {
            that.code_word = 120
            that.showInfo = true
            setInterval(function() {
              if (that.code_word > 0) {
                that.code_word--
              }
              if (that.code_word === 0) {
                that.showInfo = false
                return
              }
            }.bind(that), 1000)
          } else {
            uni.showToast({
              title: '手机验证码获取失败',
              icon: 'none'
            })
          }
        }
      })
    }
  }
}
